html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  background: var(--background-color);
}

*,
*::before,
*::after {
  box-sizing: inherit;
  color: var(--foreground-color);
  font-family: Poppins, system-ui;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.3;
}

a {
  color: var(--primary-color);
}

:root {
  --html-color: #e34f26;
  --css-color: #1572b6;
  --js-color: #f7df1e;
  --ts-color: #3178c6;
  --node-color: #339933;
  --react-color: #61dafb;
  --next-color: #ffffff;
  --scss-color: #cc6699;
  --bash-color: #4eaa25;
  --php-color: #777bb4;
  --mongo-color: #47a248;
  --mysql-color: #4479a1;
  --background-color: #f7f7f7;
  --foreground-color: #1b1c1e;
  --navbar-background-color: rgba(247, 247, 247, 0.3);
  --primary-color: #1c73f3;
  --percentage-gradients: 80%;
  --percentage-alpha-bg-skills: 6%;
}

@media screen and (prefers-color-scheme: dark) {
  :root {
    --background-color: #1b1c1e;
    --foreground-color: #f7f7f7;
    --navbar-background-color: rgba(27, 28, 30, 0.3);
    --percentage-gradients: 24%;
  }
}
@keyframes vertical-arrow {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-0.25rem);
  }
  75% {
    transform: translateY(0.25rem);
  }
}
@font-face {
  font-family: Poppins;
  font-weight: 400;
  src: url(fonts/Poppins-Regular.ttf);
}
@font-face {
  font-family: Poppins;
  font-weight: 500;
  src: url(fonts/Poppins-Medium.ttf);
}
@font-face {
  font-family: Poppins;
  font-weight: 700;
  src: url(fonts/Poppins-Bold.ttf);
}
.button {
  border: 0;
  outline: 0;
  border-radius: 0.375rem;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  text-decoration: none;
}
.button__primary {
  background-color: var(--primary-color);
  color: #fff;
}
.button__ghost {
  background-color: transparent;
}

.navbar {
  width: 100%;
  position: fixed;
  padding: 1.5rem 0;
  top: 0;
  display: grid;
  place-items: center;
  gap: 0.75rem;
  z-index: 1;
  backdrop-filter: saturate(180%) blur(27px);
  box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.12);
  background-color: var(--navbar-background-color);
}
.navbar--hidden {
  top: -100%;
  visibility: 0;
  opacity: 0;
}
.navbar-item {
  text-decoration: none;
  color: var(--foreground-color);
}
.navbar-item:not(:last-child) {
  margin-right: 1rem;
}

.landing {
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
  display: relative;
}
.landing::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 200px;
  height: 200px;
  background: rgba(171, 151, 249, var(--percentage-gradients));
  border-radius: 50%;
  filter: blur(75px);
  transform: translate(40%, -40%);
}
.landing::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 200px;
  height: 200px;
  background: rgba(106, 160, 240, var(--percentage-gradients));
  border-radius: 50%;
  filter: blur(75px);
  transform: translate(-40%, 40%);
}
.landing__content {
  display: inline-block;
  position: relative;
  z-index: 1;
}
.landing__image {
  display: block;
  width: 100%;
  min-height: 160px;
  max-width: 160px;
  margin: 0 auto;
}
.landing__title {
  font-size: clamp(1.75rem, 1vw + 1rem, 2.25rem);
  text-align: center;
  margin-bottom: 0;
}
.landing__subtitle {
  font-size: clamp(1.125rem, 1vw + 1rem, 1.5rem);
  font-weight: 400;
  text-align: center;
}
.landing__nav {
  margin-bottom: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.landing__nav-item {
  text-decoration: none;
  color: var(--foreground-color);
}
.landing__nav-item:not(:last-child) {
  margin-right: 0.75rem;
}
.landing__social {
  display: grid;
  grid-auto-flow: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}
.landing__social-item {
  width: 32px;
  height: 32px;
  color: var(--foreground-color);
  border-radius: 0.375rem;
  background: var(--background-color);
  display: grid;
  place-items: center;
}
.landing__arrow-icon {
  color: var(--foreground-color);
  position: absolute;
  bottom: 1.125rem;
  animation: vertical-arrow 1.8s linear infinite;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 24px;
}

.about-me {
  width: 100%;
  min-height: 100vh;
  display: grid;
  place-items: center;
  padding-top: 4rem;
}
.about-me__content {
  padding: 2rem;
  max-width: 1140px;
  display: grid;
  gap: 3rem;
  grid-template-areas: "title title" "avatar avatar" "info info";
}
.about-me__title {
  margin: 0;
  grid-area: title;
}
.about-me__avatar {
  display: block;
  position: relative;
  margin: 0 auto;
  grid-area: avatar;
}
.about-me__avatar::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  background: rgba(171, 151, 249, var(--percentage-gradients));
  filter: blur(128px);
  z-index: -1;
}
.about-me__image {
  margin: 0 auto;
  position: relative;
  max-width: 280px;
  width: 100%;
}
.about-me__info {
  width: 100%;
  grid-area: info;
}
@media screen and (min-width: 768px) {
  .about-me {
    padding: 0;
  }
  .about-me__content {
    grid-template-areas: "title title" "avatar info";
  }
}

.skills {
  width: 100%;
  min-height: 100vh;
  display: grid;
  place-items: center;
  padding-top: 4rem;
  position: relative;
  overflow: hidden;
}
.skills::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 200px;
  height: 200px;
  background: rgba(171, 151, 249, var(--percentage-gradients));
  border-radius: 50%;
  filter: blur(75px);
  transform: translate(120%, -32%) scale(1.6);
}
.skills::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 200px;
  height: 200px;
  background: rgba(106, 160, 240, var(--percentage-gradients));
  border-radius: 50%;
  filter: blur(75px);
  transform: translate(-120%, 32%) scale(1.6);
}
.skills__content {
  padding: 2rem;
  width: 100%;
  max-width: 1140px;
  display: grid;
  gap: 3rem;
  grid-template-rows: auto 1fr;
}
.skills__technologies {
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 1.5rem;
  justify-content: center;
}
.skills__title {
  margin: 0;
}
.skills__item {
  list-style: none;
  display: grid;
  gap: 1em;
  border-radius: 0.375rem;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 500;
}
.skills__item--html {
  background-color: rgba(227, 79, 38, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(227, 79, 38, 0.32);
}
.skills__item--css {
  background-color: rgba(21, 114, 182, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(21, 114, 182, 0.32);
}
.skills__item--javascript {
  background-color: rgba(247, 223, 30, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(247, 223, 30, 0.32);
}
.skills__item--typescript {
  background-color: rgba(49, 120, 198, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(49, 120, 198, 0.32);
}
.skills__item--node {
  background-color: rgba(51, 153, 51, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(51, 153, 51, 0.32);
}
.skills__item--react {
  background-color: rgba(97, 218, 251, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(97, 218, 251, 0.32);
}
.skills__item--next {
  background-color: rgba(0, 0, 0, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(0, 0, 0, 0.32);
}
.skills__item--scss {
  background-color: rgba(204, 102, 153, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(204, 102, 153, 0.32);
}
.skills__item--bash {
  background-color: rgba(78, 170, 37, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(78, 170, 37, 0.32);
}
.skills__item--php {
  background-color: rgba(119, 123, 180, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(119, 123, 180, 0.32);
}
.skills__item--mongo {
  background-color: rgba(71, 162, 72, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(71, 162, 72, 0.32);
}
.skills__item--mysql {
  background-color: rgba(68, 121, 161, var(--percentage-alpha-bg-skills));
  border: 1px solid rgba(68, 121, 161, 0.32);
}
.skills__image {
  margin: 0 auto;
  width: 80px;
  height: 80px;
  padding: 1rem;
  backdrop-filter: blur(120px);
  border-radius: 0.375rem;
}
.skills__name {
  text-align: center;
  font-weight: 500;
}

.projects {
  width: 100%;
  min-height: 100vh;
  display: grid;
  place-items: center;
  overflow: hidden;
}
.projects__content {
  padding: 2rem;
  width: 100%;
  max-width: 1140px;
  display: grid;
  gap: 2rem;
  grid-template-rows: auto 1fr;
}
.projects__title {
  padding-top: 3rem;
}
.projects__collection {
  padding: 0;
}

.project {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.project:nth-child(2n) {
  flex-direction: row-reverse;
}
.project__image {
  width: 100%;
  position: relative;
  border-radius: 0.375rem;
  margin: 0;
}
.project__image::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  background: rgba(106, 160, 240, var(--percentage-gradients));
  border-radius: 50%;
  filter: blur(75px);
  transform: scale(1.2);
  z-index: -1;
}
.project__image img {
  width: 100%;
  position: relative;
  border-radius: 0.375rem;
}
.project__actions {
  display: flex;
  justify-content: flex-end;
}
.project__actions a:not(:last-child) {
  margin-right: 1rem;
}
.project:not(:first-child) {
  margin: 30vh 0;
}
.project:last-child {
  margin: 30vh 0;
  margin-bottom: 4rem;
}
@media screen and (min-width: 768px) {
  .project {
    flex-wrap: nowrap;
  }
  .project__image {
    margin-right: 2.5rem;
  }
  .project:nth-child(2n) .project__image {
    margin: 0;
    margin-left: 2.5rem;
  }
}